import React, {useEffect, useState, useRef} from "react";
import ARTEx from "../components/ARTEx 3D";

function App() {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    // Cleanup function
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  
  const page1Ref = useRef(null);

  return (
    <div className="page" ref={page1Ref} style={{marginTop:"50px", height:"calc(100vh - 100px)", display:"flex", borderBottom:"3px solid var(--font-color)", width:"100%", overflow:"hidden"}}>  
      <div className="page1-organise"> 
          <h2 className="homePage-title">Top Project Pick</h2> 
          <h6 className="homePage-h6">All-terrain Robotic Transportation and Exploration System (University Project)</h6>
          <h3 className="homePage-h3">ARTEx</h3>
          <div className="homePage-display" style={{marginTop:"40px"}}><ARTEx/></div>
            <button className="homePage-button" onClick={""}><h3 className="homePage-text">View More Projects</h3></button>
      </div>
    </div>   
  );
}

export default App;
