import React, {useState, useEffect} from "react";
import { motion, useScroll } from "framer-motion"

function Header({ page1Height }) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isBlackout, setBlackout] = useState(false);
  const [isMenu, setIsMenu] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const { scrollYProgress } = useScroll();

  useEffect(() => {
    const handleResize = () => {
      setIsMenu(window.innerWidth < 440);
    };

    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };
    window.addEventListener('resize', handleResize);
    window.addEventListener('scroll', handleScroll);

    handleResize(); // Call initially
    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('scroll', handleScroll);
    };
  }, [page1Height]);


  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
    setBlackout(!isBlackout);
  };

  return (
    <header>
      <div className="header-container">
      <div className={scrolled ? 'header scrolled' : 'header'}>
        <div>
        <h1>Samuel Akintomide</h1>
        <h4>Robotics Engineer, Developer, and Designer</h4> 
        </div>
        <div className="nav-buttons-container">
            <button className="nav-buttons" onClick="">Home</button>
            <button className="nav-buttons" onClick="">Projects</button>
            <button className="nav-buttons" onClick="">Information </button>
            <button className="nav-buttons" onClick="">Contact</button>
        </div>
      </div>
      <motion.div className="progress-bar" style={{ scaleX: scrollYProgress }} />
      </div>

      {isMenu && (<>
        <button className="menuButton" onClick={toggleMenu}>MENU</button>
        
      </>)}
    </header>
  );
}

export default Header;
